.SoloWaiting {
	display: inline-block;
	width: 90%;
	margin:20px 10px 12px 50px; 
	border: 1px solid grey;
	-webkit-box-shadow: 1px 2px 3px #ccc;
	        box-shadow: 1px 2px 3px #ccc;
	border-radius: 4px;
	padding: 16px;
	text-align: center;
	font-family: 'Helvetica', sans-serif;
	background: white;
	color: black;

}

.SoloWaiting h1{
	font-family: 'Montserrat', sans-serif;
	clear: both;
}

.AnswersButton {
	font-size: 22px;
	border: none;
	background: none;
	color: gray;
	float: right;
	}

.AnswersButton:hover {
	
	color: black;
	-webkit-box-shadow: none;
	        box-shadow: none;

}

@media screen and (max-width: 640px) {

	.SoloStart {
		width: 22%
	}
	.SoloWaiting {
		margin-left: 9px;
		width: 87%;
	}

	.BackButton {

	}

	.AnswersButton {
		margin: 0;
		padding: 0;
		width: 30%;
		
	}

	.BackButton {
		margin: 0;
		padding: 0;
		width: 30%;
	}

}


