.player {

	display: inline-block;
	width: 23%;
	margin-left: 25px;
}

.player svg {
	stroke: white;
	-webkit-transform: rotate(-90deg);
	    -ms-transform: rotate(-90deg);
	        transform: rotate(-90deg);
}


.name {
	position:absolute;
	margin-top: -125px;
	margin-left: 33px;
	font-family: Open Sans, sans-serif;
	font-size: 35px;
	text-align: center;
	color: white;
}

.score {
	position:absolute;
	margin-top: -85px;
	margin-left: 80px;
	font-family: Open Sans, sans-serif;
	font-size: 18px;
	text-align: center;
	color: #655;
}


