.BackButton {
	font-size: 22px;
	border: none;
	background: none;
	color: gray;
	float: left;
}

.BackButton:hover {
	
	color: black;
	-webkit-box-shadow: none;
	        box-shadow: none;

}


.Waiting {
	display: inline-block;
	width: 90%;
	margin:20px 10px 12px 50px; 
	border: 1px solid grey;
	-webkit-box-shadow: 1px 2px 3px #ccc;
	        box-shadow: 1px 2px 3px #ccc;
	border-radius: 4px;
	padding: 16px;
	text-align: center;
	font-family: 'Helvetica', sans-serif;
	background: white;
	color: black;
}

.Waiting h1{
	text-align: center;
	clear: both;
}

.Waiting h2{
	font-weight: lighter;
	clear: both;
}

.Waiting h3{
	font-family: 'Helvetica', sans-serif;
	font-weight: lighter;
	font-size: 40px;

}

.Waiting button{
	-webkit-font-kerning: normal;
	        font-kerning: normal;
}


.Team {

	display: inline-block;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 25%;
	margin:20px; 
	border: 1px solid grey;
	-webkit-box-shadow: 1px 2px 3px #ccc;
	        box-shadow: 1px 2px 3px #ccc;
	border-radius: 4px;
	padding: 16px;
	text-align: center;
	font-family: 'Helvetica', sans-serif;
	background: white;
	color: black;
}

.Player {
	border: 2px solid grey;
	border-radius: 8px;
	
	text-align: center;
	margin: 3px;
	font-family: 'Open Sans', sans-serif;

}

.Player p{
	font-size: 18px;
	margin: 12px 10px 12px 0px;

}


@media screen and (max-width: 640px) {

.WaitingWrapper {
	width: 100%

}

.Waiting {
	display: inline-block;
	width: 90%;
	margin:0 0 0 5px; 
	border: 1px solid grey;
	-webkit-box-shadow: 1px 2px 3px #ccc;
	        box-shadow: 1px 2px 3px #ccc;
	border-radius: 4px;
	padding: 16px;
	text-align: center;
	font-family: 'Helvetica', sans-serif;
	background: white;
	color: black;
}

.Waiting h1{
	text-align: center;
	clear: both;
}

.Waiting h2{
	font-weight: lighter;
	clear: both;
}

.Waiting h3{
	font-family: 'Helvetica', sans-serif;
	font-weight: lighter;
	font-size: 40px;

}

.Waiting button{
	width: 30%;
	-webkit-font-kerning: normal;
	        font-kerning: normal;
}

button.BackButton {
	margin-left: -12px;
}

}


